.main-nav__toggle {
  display: block;
  width: 20px;
  height: 50px;
  background-color: #fff;
  padding: 0;
  border: none;
  position: relative;
  margin-left: 40px; }
  @media (min-width: 768px) {
    .main-nav__toggle {
      display: none; } }
  .main-nav__toggle span,
  .main-nav__toggle span:before,
  .main-nav__toggle span:after {
    position: absolute;
    height: 2px;
    width: 20px;
    background-color: #000;
    left: 0;
    top: 50%;
    margin-top: -1px;
    -webkit-transition: 0.2s;
    transition: 0.2s; }
  .main-nav__toggle span:before {
    content: '';
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px); }
  .main-nav__toggle span:after {
    content: '';
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }

.main-nav__toggle--close span {
  height: 0; }

.main-nav__toggle--close span:before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg); }

.main-nav__toggle--close span:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

